.film-backdrop {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;

  @media screen and (min-width: 768px) {
    padding: 200px 50px;
  }
  @media screen and (min-width: 1024px) {
    padding: 100px 70px;
  }
}

.noOverview {
  @media screen and (min-width: 1024px) {
    position: relative;
    left: 100px;
  }
}

.cinema__image {
  width: 400px;
}

.film-modal {
  position: relative;

  padding: 48px 20px 41px 20px;
  overflow: auto;
  max-height: calc(100vh - 40px);
  min-width: 260px;
  max-width: 280px;

  background: var(--header-links);

  @media screen and (min-width: 768px) {
    min-width: 600px;
    max-width: 618px;
    padding: 42px 30px 40px 28px;
  }

  @media screen and (min-width: 1024px) {
    min-width: 822px;
    max-width: 882px;
    padding: 50px 30px 40px 30px;
    overflow: hidden;
  }
}

.cinema-card {
  @media screen and (min-width: 768px) {
    display: flex;
  }
}
.cinema__image {
  width: 240px;
  max-height: 357px;
  display: block;
  margin-bottom: 30px;
  border-radius: 5px;
  object-fit: cover;

  @media screen and (min-width: 768px) {
    width: 264px;
    max-height: 373.55px;

    margin-bottom: 0px;
    margin-right: 32px;
  }

  @media screen and (min-width: 1024px) {
    width: 396px;
    max-height: 477.8px;

    margin-right: 30px;
  }
}

.info-name {
  @include font(500, 12px, 16px);

  color: #8c8c8c;
}

.info-item1 {
  margin-bottom: 20px;

  @include font(500, 20px, 23px);

  text-transform: uppercase;

  @media screen and (min-width: 1024px) {
    @include font(500, 30px, 35px);
  }
}

.info-item2 {
  display: flex;
  align-items: baseline;

  margin-bottom: 10px;
}

.info-data2 {
  display: flex;
  align-items: baseline;

  margin-left: 41px;
  font-weight: normal;

  @media screen and (min-width: 1024px) {
    margin-left: 85px;
  }
}

.info-data2-acent1 {
  display: flex;
  width: 36px;
  height: 16px;
  border-radius: 5px;
  border: none;

  @include font(500, 12px, 14.06px);

  align-items: center;
  text-align: center;
  justify-content: center;
  text-transform: uppercase;

  margin-right: 3px;

  background-color: var(--header-links-accent);
  color: var(--header-links);
}

.info-data2-acent2 {
  display: flex;
  width: 36px;
  height: 16px;
  border-radius: 5px;
  border: none;

  @include font(500, 12px, 14.06px);

  align-items: center;
  text-align: center;
  justify-content: center;
  text-transform: uppercase;

  margin-left: 3px;

  background-color: var(--pag-button-bg);
}

.info-item3 {
  display: flex;
  align-items: baseline;

  margin-bottom: 10px;
}

.info-data3 {
  margin-left: 53px;

  @include font(500, 12px, 14.06px);

  text-transform: uppercase;

  @media screen and (min-width: 1024px) {
    margin-left: 97px;
  }
}

.info-item4 {
  display: flex;
  align-items: baseline;

  margin-bottom: 10px;
}

.info-data4 {
  margin-left: 39px;

  @include font(500, 12px, 16px);

  text-transform: uppercase;

  @media screen and (min-width: 1024px) {
    margin-left: 83px;
  }
}

.info-item5 {
  display: flex;
  // align-items: start;

  margin-bottom: 20px;
}

.info-data5 {
  margin-left: 76px;
  word-wrap: break-word;
  @include font(500, 12px, 16px);
  max-width: 110px;

  @media screen and (min-width: 1024px) {
    margin-left: 120px;
  }
}

.info-item6 {
  display: flex;
  flex-direction: column;

  margin-bottom: 21px;
}

.info-item6 .info-name {
  margin-bottom: 10px;

  @include font(500, 12px, 16px);

  text-transform: uppercase;
  color: var(--main-black);
}

.info-data6 {
  @include font();
}

.modal__button-add {
  display: flex;
  justify-content: space-between;

  @media screen and (min-width: 768px) {
    justify-content: end;
  }

  @media screen and (min-width: 1024px) {
    margin-right: 129px;
    margin-top: -60px;
  }
}

.film-modal-button {
  display: flex;
  align-items: center;
  justify-content: center;

  width: 112.58px;
  height: 64px;

  padding-left: 8.28px;
  padding-right: 8.28px;
  border-radius: 5px;

  @include font(500, 12px, 16px);
  text-transform: uppercase;
  cursor: pointer;
  @include animation2(background-color, color);

  @media screen and (min-width: 768px) {
    width: 125px;
    height: 44px;
  }
}

.add-to-watched {
  background-color: var(--header-links-accent);
  color: var(--header-links);
  border: 1px solid transparent;

  &:hover {
    border-color: var(--main-black);
    background-color: var(--header-links);
    color: var(--main-black);
  }

  @media screen and (min-width: 768px) {
    margin-right: 14.94px;
  }
}

.delete {
  @extend .add-to-watched;
  margin-left: 15px;
}

.add-to-queue {
  border: 1px solid var(--main-black);

  &:hover {
    border-color: transparent;
    background-color: var(--header-links-accent);
    color: var(--header-links);
  }
}
